.location-form {
  padding: 4rem 3rem;
  background-color: rgba(50, 160, 180, 0.7);
  border-radius: 0 0 4px 4px;
  display: flex;
  align-items: center;
  border: 1.8px solid rgba(0, 0, 0, 0.78);
  border-width: 0 1.8px 1.8px 1.8px;
  justify-content: space-around;
}

.location-form__input {
  font-size: 2rem;
  border: 0;
  text-decoration: none;
  outline: none;
  padding: 1rem 4rem;
  width: 30%;
  border-radius: 5px;
  border-bottom: 3px solid transparent;
  color: rgba(0, 0, 0, 0.8);
  letter-spacing: 0.2rem;
  font-weight: 500;
  text-transform: uppercase;
  transition: all 0.5s;
}

.location-form__input:focus {
  border-bottom: 3px solid rgba(245, 96, 44, 1);
  border-radius: 5px 5px 0 0;
}

.location-form__input::-webkit-input-placeholder {
  color: rgba(0, 0, 0, 0.4);
  letter-spacing: 0.2rem;
  font-weight: 500;
  text-transform: uppercase;
}

.location-form__btn {
  background-color: rgba(25, 170, 25, 0.9);
  font-size: 2rem;
  border: 0;
  cursor: pointer;
  padding: 1rem 2rem;
  border-radius: 15px;
  color: white;
  letter-spacing: 0.2rem;
  text-transform: uppercase;
  font-weight: 500;
  transition: all 0.4s;
}

.location-form__btn:hover {
  transform: translateY(-2px);
  box-shadow: 0 0.7rem 1rem rgba(0, 0, 0, 0.2);
}

.location-form__btn:active {
  transform: translateY(2px);
  box-shadow: 0 0.7rem 1rem rgba(0, 0, 0, 0.3);
}

.location-form__btn:focus {
  outline: 0;
  box-shadow: 0 0 3px 2px rgba(245, 96, 44, 1);
}
